#image-box{
    max-height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    /*이미지 박스 가운데 정렬*/
}

#image-box > img{
    width: 100%;
    max-height: 300px;
    object-fit: contain;
}

#profile-box{
    padding: 16px;
    margin-top: 8px;
    border-bottom: 1px solid #e9ecef; 
            
}
#profile-box > img{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
}

#profile-box > span{
    font-size: 10px;
    margin-left: 8px;
    font-weight: 600;
}

#contents-box{
    padding: 16px;

}

#name{
    font-size: 20px;
    font-weight: 400;
}

#price{
    font-size: 16px;
    font-weight: 600;
}

#description {
    margin-top: 16px;
    font-size: 16px;
}
#createdAt {
    font-size: 12px;
    color: rgb(204,204,204);
}
#purchase-button{
    margin-top: 8px;
    margin-bottom: 16px;
}