#upload-container{
    padding-top: 36px;
}

#upload-img-placeholder{
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    border: 1px solid rgb(230,230,230);
    background : rgb(250,250,250);
    margin-left: 16px;
}

#upload-img-placeholder > span{
    font-size: 14px;
    color: rgb(195,194,204);
}
.upload-name{
    width: 200px;
}
.upload-label{
    font-size: 18px;
    width: 80px;
}

#product-description{
    height: 150px;
}

#submit-button{
    float: right;
    background-color: rgb(30,120,255);
    color: white;
    border: none;
    margin-top: 16px;
}
#upload-img{/*이미지 크기 조절*/
    width: 200px;
    height: 200px;
    margin-left: 16px;
    object-fit: contain; /*해당 이미지 크기를 가로세로 맞도록 설정*/
}